import React, { useState, useEffect } from "react"
import { Router } from "@reach/router"
import ProductInfo from "../modules/productsInfo"
import Nav from "../components/nav"
import Axios from "../../axios"
import { BASE_URL } from "../../config"
import Footer from "../components/footer"
import BreadcumHeader from "../components/breadcumHeader"
import { Link, navigate } from "gatsby"


export default function Mainproduct() {

      const [ProductCats, setProductCats] = useState([])
      const [ProductTitles, setProductTitles] = useState([])

  useEffect(() => {
    async function fetchData() {
      
      const products = await Axios.get("/products")
      const productTitles = await Axios.get("/product-titles")
     
     
      setProductCats(
        [...products.data].map(p => {
          return { _id: p._id, name: p.name }
        })
      )
      setProductTitles(
        [...productTitles.data].map(t => {
          return { _id: t._id, name: t.name, cat_id: t.product_category._id }
        })
      )
    
      return products
    }
    fetchData()
  }, [])

  const onClickProductTitle = _id => {
    navigate(`/product/${_id}/`)
  }

    return (

      <div>
           <BreadcumHeader title={"Products"} heroImageName={"about"} />

        <div className="container" style={{marginBottom: '50px'}}>
          <div className="row" style={{marginTop: '20px'}}>

             {ProductCats?.map((document, i) => (
                    <>
                        <div className="col-md-3" style={{marginTop: '30px'}}>
                        <div className="image-product">
                            <ul className="nav flex-column ul-mail">
                            <span  key={document._id} className="text-uppercase text-white h2-mail">{document.name}</span>
                                {ProductTitles?.map((item, i) => (
                                      <>
                                      {item.cat_id === document._id ? (
                                      <li classname="li-mail" key={item._id}
                                      onClick={() =>{
                                        onClickProductTitle(item._id)
                                      }
                                        
                                      } >
                                      <a className="nav-link navlink-a a-mail" href="#"><span className="nav-boders">{item.name}</span></a>
                                       </li>
                                        ) : (
                                       <> </>
                                  )}
                                </>
                              ))}
                          </ul>
                          </div>
                        </div>
                    </>
              ))}

  </div>
  </div>
        <Footer /> </div>
    )
}